body{padding:0;font-family:sans-serif}body #root{padding:0 2rem}body #root .license-text{background-color:#eee;border-top:20px solid #fff;padding:10px}body #root #main{display:flex;flex-direction:column;width:15rem;padding-left:40px}body #root #main>*{padding-top:1rem}body #root #main .diff{display:flex;align-items:center}body #root li{padding-top:4px}body #root .between{display:flex;align-items:center;justify-content:space-between}body #root .copyright{position:fixed;left:0;bottom:0;width:100%;font-size:12px;text-align:center;margin-bottom:5px}body #root .copyright a{text-decoration:none;opacity:.65}body #root .copyright a:visited{color:#000}@media(max-width: 930px),(orientation: portrait){body #root{padding:0}}
